.card-1-background {
  background-image: url('../../images/card-1-background.png');
}
.card-2-background {
  background-image: url('../../images/card-2-background.png');
}
.card-3-background {
  background-image: url('../../images/card-3-background.png');
}

.card-content {
  height: 30rem;
  max-height: 371px;
  max-width: 450px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card .card-content {
  padding: 0;
}

#card-content-text {
  margin: 24px;
  line-height: 300%;
}
#card-content-text > span {
  font-size: 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #f2f2f2;
}

#card-content-text > p {
  font-size: 1.2rem;
}
