.toast {
  background-color: #4bb543 !important;
  border: 2px solid white;
}

#online-education-modal {
  height: 70vh;
}

#online-education-modal ul {
  padding-inline-start: 0;
  /* font-size: 4rem; */
}

#online-education-modal .dropdown-content li {
  text-align: center;
  color: black;
}

#online-education-modal .dropdown-content li > a,
.dropdown-content li > span {
  color: black;
}

#online-education-modal .input-field > label {
  text-align: center;
  color: #0000004d;
}

#online-education-modal input {
  text-align: center;
  font-family: 'Cairo', sans-serif;
}

#online-education-modal .active,
.dot:hover {
  background-color: transparent;
}
