.online-education-main {
  background: url('../../images/online-education-page-background-strip.jpg')
    center repeat-x;
  background-size: auto 100%;
}

.online-education-top-wrapper {
  display: flex;
  width: 70vw;
  margin: auto;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: space-evenly;
  background-color: #f9f9f9;
  box-shadow: 7px 0px 20px 0px rgb(182, 181, 181);
  padding: 6rem 0 20rem 0;
}

.online-education-right-element {
  width: 40%;
  text-align: end;
}
.online-education-left-element {
  width: 40%;
}
.online-education-left-element img {
  border-radius: 5%;
  box-shadow: -5px 5px 1rem 0.3rem rgba(0, 0, 0, 0.2);
}

@media (max-width: 950px) {
  .online-education-right-element {
    width: 90%;
  }
  .online-education-left-element {
    width: 90%;
  }
  .online-education-top-wrapper {
    padding: 2rem 0 2rem 0;
  }
}

.online-education-button-div {
  text-align: start;
  margin-bottom: 2rem;
}
.online-education-button-div button {
  width: 9rem;
}
