.card-container {
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  margin-top: 5rem;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.card-item {
  width: 25%;
}

@media (max-width: 1060px) and (min-width: 801px) {
  .card-item {
    width: 30%;
  }
}

@media (max-width: 800px) and (min-width: 601px) {
  .card-container {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 5rem;
    justify-content: center;
  }
  .card-item {
    width: 70%;
  }
}

@media (max-width: 600px) {
  .card-container {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 5rem;
    justify-content: center;
  }
  .card-item {
    width: 100%;
  }
}

.cards-group {
  margin: 10rem 0;
}

.cards-group > h3 {
  font-size: 2.5rem;
}
.cards-group > h6 {
  font-size: 1rem;
}
