* {
  box-sizing: border-box;
}

/* Slideshow container */
.slideshow-container {
  max-width: 100vw;
  min-height: 100vh;
  /* min-height: 600px; */
  background-color: #ccc;
  overflow: hidden;
  position: relative;
  margin: auto auto 0.5rem auto;
}

/* Hide the images by default */
.mySlides {
  display: none;
}

/* Next & previous buttons */
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
  background-color: transparent;
  border: none;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

/* Caption text */
.text {
  color: #f2f2f2;
  /* color: #000; */
  /* font-size: 15px; */
  padding: 8px 12px;
  position: absolute;
  top: 60%;
  right: 0;
  /* width: 100%; */
  text-align: end;
}

@media (max-width: 500px) {
  .text {
    top: 50%;
  }
  .text h4 {
    font-size: 1.5rem;
  }
  .text h6 {
    font-size: 0.75rem;
  }
}

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active,
.dot:hover {
  background-color: #717171;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 2s;
}

@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

.carousel-image-1 {
  content: url('../../images/option-1.png');
  width: 100%;
}
.carousel-image-2 {
  content: url('../../images/option-2.png');
  width: 100%;
}
.carousel-image-3 {
  content: url('../../images/option-3.png');
  width: 100%;
}

@media (max-width: 450px) {
